<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process195"></a>

<p><br></p>
<p><span class="rvts17">慷慨的出价（</span><span class="rvts50">A Generous Offer</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:515,Y:178</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：瑞克（</span><span class="rvts20">Ryker</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">从黑坑洞中，带回瑞克所需要的石板</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1229.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">瑞克首次出现的位置是在石墓花园的洋馆中，在一楼他的办公桌前。与他交谈，他会一次性布置慷慨的出价（</span><span class="rvts20">Generous Offer</span><span class="rvts21">）和午夜挖掘（</span><span class="rvts20">Midnight Oil</span><span class="rvts21">）两个支线任务。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1230.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">过一段时间后，瑞克会在办公桌区域留下一封信，说他去了他的地下密室，并等待我们给他带来他想要的东西。密室的通道在如图这个位置（就在离他的办公桌不远的地方）。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1231.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">瑞克需要的神秘石板就在黑坑洞深处，远古神庙永恒者的石棺旁的石头抽屉内。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1232.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">完成瑞克交给我们的这个</span><span class="rvts20"></span><span class="rvts21">慷慨的报价（</span><span class="rvts20">Generous Offer</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">任务，并把石板交给他后，他会教我们增强</span><span class="rvts20">1</span><span class="rvts21">点的源力值。无论是否学习，我们总可以杀掉瑞克。瑞克是一个比较可恨但又比较可怕的亡灵法师，通过秘术控制了死人的灵魂（他的随从都是活死人），除掉他以免这个古怪的亡灵法师继续折磨其它的生灵。毕竟在他的地下室藏着很多值钱和有用的装备。</span>
<br><span class="rvts21">另外，玩家打败他后，可以在他的洋馆尽情的收集金银餐具，名画等可以卖出高价钱的物品，瑞克的地下室和洋馆是整个死亡海岸地图上难得的可以发家致富的地方。该任务需要玩家带回瑞克所需要的石板，石板位置位于黑坑深处的永恒大神的石棺旁的小石头存储箱内。（详见黑坑洞探索篇）另外，该石板也是另一个任务</span><span class="rvts20"></span><span class="rvts21">阿尔米亚的请求</span><span class="rvts20"></span><span class="rvts21">支线中的任务物品。</span>
<br><span class="rvts21">尝试交给瑞克石板后学到额外的源力技能点，再杀掉他，也会重新拾取石板。之后，交给阿尔米亚并劝说阿尔米亚登上复仇女神号。下一章节在无名岛，阿尔米亚将使用该石板合成另一件远古神器。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



